export function queryExtendedBookings(
  httpClient: HttpClient
): QueryExtendedBookingsSignature {
  return (query: CommonQueryV2, options?: QueryExtendedBookingsOptions) =>
    universalQueryExtendedBookings(
      query,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface QueryExtendedBookingsSignature {
  /**
   * Retrieves a list of bookings, including additional extended information, given the provided paging, filtering, and sorting.
   *
   * `queryExtendedBookings()` doesn't use a query builder, instead it uses [API query language](https://dev.wix.com/docs/sdk/articles/work-with-the-sdk/api-query-language#with-the-api-query-language).
   *
   * Up to 100 extended bookings can be returned per request.
   *
   * `queryExtendedBookings()` runs with these defaults, which you can override:
   *
   * - `createdDate` sorted in `DESC` order
   * - `cursorPaging.limit` is `50`
   *
   *
   * You can retrieve information about which actions the customer can perform
   * for the bookings. To do so, pass `withBookingAllowedActions` as `true`.
   *
   *
   * For field support, see
   * [supported filters](https://www.wix.com/velo/reference/wix-bookings-v2/extendedbookings/supported-filters)
   * for more information.
   *
   * You can specify a filter only once per query. If you specify a filter
   * more than once, only the first filter determines the extended bookings that are returned.
   *
   * When filtering by date, you must use [UTC time](https://en.wikipedia.org/wiki/Coordinated_Universal_Time).
   * @param - Information about filters, paging, and sorting.
   * @param - Additional options for performing the query.
   */
  (query: CommonQueryV2, options?: QueryExtendedBookingsOptions): Promise<
    NonNullablePaths<
      QueryExtendedBookingsResponse,
      | `extendedBookings`
      | `extendedBookings.${number}.booking.totalParticipants`
      | `extendedBookings.${number}.booking.status`
      | `extendedBookings.${number}.booking.paymentStatus`
      | `extendedBookings.${number}.booking.selectedPaymentOption`
      | `extendedBookings.${number}.booking.createdBy.anonymousVisitorId`
      | `extendedBookings.${number}.booking.createdBy.memberId`
      | `extendedBookings.${number}.booking.createdBy.wixUserId`
      | `extendedBookings.${number}.booking.createdBy.appId`
      | `extendedBookings.${number}.allowedActions.cancel`
      | `extendedBookings.${number}.allowedActions.reschedule`
      | `extendedBookings.${number}.attendance.status`
      | `extendedBookings.${number}.attendance.numberOfAttendees`
      | `extendedBookings.${number}.bookingFeeDetails.cancellationFeeStatus`
      | `extendedBookings.${number}.order.number`
      | `extendedBookings.${number}.order.status`
      | `extendedBookings.${number}.order.paymentStatus`
      | `extendedBookings.${number}.waiver.status`
      | `extendedBookings.${number}.invoices.orderId`
      | `extendedBookings.${number}.transactions.orderId`,
      6
    >
  >;
}

export function countExtendedBookings(
  httpClient: HttpClient
): CountExtendedBookingsSignature {
  return (options?: CountExtendedBookingsOptions) =>
    universalCountExtendedBookings(
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface CountExtendedBookingsSignature {
  /**
   * Counts the number of bookings matching the specified filters.
   *
   *
   * For field support see
   * [supported filters](https://dev.wix.com/api/rest/wix-bookings/bookings-reader-v2/supported-filters)
   * for more information.
   */
  (options?: CountExtendedBookingsOptions): Promise<
    NonNullablePaths<CountExtendedBookingsResponse, `count`, 2>
  >;
}

